import { useState } from "react";
import styled from "styled-components";
import Loading from "../components/study/Loading";
import ArrowLeftBlack from "../assets/ArrowLeft.svg";
import ArrowRightBlack from "../assets/ArrowRight.svg";
import ArrowLeftWhite from "../assets/ArrowLeftWhite.svg";
import ArrowRightWhite from "../assets/ArrowRightWhite.svg";
import { HStack, Image, Button, useColorModeValue } from "@chakra-ui/react";
import Three from "../components/study/Three";

const StudyWrap = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 6fr;
    grid-template-areas:
        " left name right "
        " center center center";
    width: 80%;
    height: 80vh;
`;

const NameWrap = styled.div`
    grid-area: name;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const LeftWrap = styled.div`
    grid-area: left;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const RightWrap = styled.div`
    grid-area: right;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const CenterWrap = styled.div`
    grid-area: center;
    display: flex;
    justify-content: center;
    align-items: center;
`;

function Study() {
    const ArrowLeft = useColorModeValue(ArrowLeftBlack, ArrowLeftWhite);
    const ArrowRight = useColorModeValue(ArrowRightBlack, ArrowRightWhite);
    const [index, setIndex] = useState(0);
    console.log(index);
    const items = [
        { name: "로딩중", content: <Loading /> },
        { name: "Three.js", content: <Three /> },
    ];

    const clickRight = () => {
        if (index < items.length - 1) {
            setIndex(index + 1);
        }
    };

    const clickLeft = () => {
        if (index > 0) {
            setIndex(index - 1);
        }
    };

    return (
        <HStack w="100%" justify="center">
            <StudyWrap>
                <NameWrap>{items[index].name}</NameWrap>
                <CenterWrap>{items[index].content}</CenterWrap>
                <LeftWrap>
                    <Button onClick={clickLeft}>
                        <Image src={ArrowLeft} />
                    </Button>
                </LeftWrap>
                <RightWrap>
                    <Button onClick={clickRight}>
                        <Image src={ArrowRight} />
                    </Button>
                </RightWrap>
            </StudyWrap>
        </HStack>
    );
}

export default Study;
