import React, { useState } from "react";
import styled from "styled-components";
import src from "../assets/menu.png";
import { AnimatePresence, motion } from "framer-motion";
import { Link } from "react-router-dom";
import { useColorMode, Button } from "@chakra-ui/react";
import { useMediaQuery } from "react-responsive";

const HeaderWrap = styled.div`
  background-color: #22223b;
  color: white;
  height: 70px;
  font-size: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
`;

const Wrap = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 80%;
  height: 100%;
  @media only screen and (max-width: 700px) {
    justify-content: space-between;
  }
`;

const Menu = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 250px;
  height: 100%;
  flex-grow: 3;
`;

const MenuItem = styled(Link)`
  text-decoration: none;
  color: white;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 20px;
  @media only screen and (max-width: 700px) {
    height: 40px;
  }
  &:hover {
    background-color: #4a4e69;
  }
`;

const Modal = styled.div`
  width: 100%;
  background-color: #4a4e69;
  position: fixed;
  top: 70px;
`;

const ContentWrap = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 110px;
  @media only screen and (max-width: 700px) {
    margin-top: 90px;
  }
`;

function Header() {
  const [index, setIndex] = useState(0);
  const [active, setActive] = useState(false);
  const { colorMode, toggleColorMode } = useColorMode();
  const isBigScreen = useMediaQuery({ query: "(min-width: 700px)" });

  return (
    <div>
      <HeaderWrap>
        <Wrap>
          {isBigScreen ? (
            <>
              <div style={{ flexGrow: 2 }}>Dong Chang Lee</div>
              <Menu>
                <MenuItem
                  to="/"
                  onClick={() => {
                    index !== 0 && setIndex(0);
                  }}
                >
                  About
                </MenuItem>
                <MenuItem
                  to="/study"
                  onClick={() => {
                    index !== 1 && setIndex(1);
                  }}
                >
                  Study
                </MenuItem>
                <MenuItem
                  to="/toys"
                  onClick={() => {
                    index !== 2 && setIndex(2);
                  }}
                >
                  Toys
                </MenuItem>
              </Menu>
              <Button size="sm" onClick={toggleColorMode}>
                {colorMode === "light" ? "Dark" : "Light"}
              </Button>
            </>
          ) : (
            <>
              <div>Dong Chang Lee</div>
              <Button size="sm" onClick={toggleColorMode}>
                {colorMode === "light" ? "Dark" : "Light"}
              </Button>
              <div onClick={() => setActive(!active)}>
                <img src={src} style={{ height: "20px", width: "25px" }} />
              </div>
            </>
          )}
        </Wrap>
      </HeaderWrap>
      {!isBigScreen && active && (
        <Modal>
          <MenuItem
            to="/"
            onClick={() => {
              index !== 0 && setIndex(0);
              setActive(!active);
            }}
            style={index === 0 ? { color: "#ffffff" } : { color: "#9A8C98" }}
          >
            About
          </MenuItem>
          <MenuItem
            to="/study"
            onClick={() => {
              index !== 1 && setIndex(1);
              setActive(!active);
            }}
            style={index === 1 ? { color: "#ffffff" } : { color: "#9A8C98" }}
          >
            Study
          </MenuItem>
          <MenuItem
            to="/toys"
            onClick={() => {
              index !== 2 && setIndex(2);
              setActive(!active);
            }}
            style={index === 2 ? { color: "#ffffff" } : { color: "#9A8C98" }}
          >
            Toys
          </MenuItem>
        </Modal>
      )}
    </div>
  );
}

export default Header;
